<template>
  <div class="login-container">
    <p>
      <input type="text" autocomplete="new-password" v-model="loginId" />
    </p>
    <p>
      <input type="password" autocomplete="new-password" v-model="loginPwd" />
    </p>
    <p>
      <button @click="handleClick">登录</button>
    </p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginId: '',
      loginPwd: '',
    }
  },
  methods: {
    async handleClick() {
      const result = await this.$store.dispatch('loginUser/login', {
        loginId: this.loginId,
        loginPwd: this.loginPwd,
      })
      if (result.data.id) {
        alert('登录成功')
        this.$router.push('/')
      }
    },
  },
}
</script>

